@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

@media screen and (max-width: 650px){
  .App {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-style: normal;
  }
  
  .App-header {
    background-color: white;
    min-height: 100vh;
    align-items: center;
    justify-content: center;
  }
  
  .dialog-box{
    height: 100%;
    width: 100%;
    border-radius: 3vh;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    column-gap: 5vh;
  }
  .con-header{
    font-weight: 700;
    font-size: 40px;
  }
  
  .content{
    display:flex;
    flex-direction: column;
    height: 100%;
    row-gap: 2.5vh;
    color: hsl(234, 29%, 20%);
    font-weight: 400;
    padding: 4vh;
  }
  
  .con-msg{
    font-size: 13px;
    word-spacing: 2px;
    line-height: 2.5vh;
  }
  
  .content > ul{
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  .content > ul > li{
    padding-left: 30px;
    background: url('../newsletter-sign-up-with-success-message-main/assets/images/icon-list.svg') left center no-repeat;
    background-size: 16px 16px; 
    font-size: 13px;
    word-spacing: 2px;
    line-height: 3.5vh;
  }
  
  .photo {
    background-image: url('../newsletter-sign-up-with-success-message-main/assets/images/illustration-sign-up-mobile.svg');
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 40vh; 
    border-radius: 1vh;
  }
  
  .email{
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  
  .email-header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .err-msg{
    color: tomato;
    font-weight: 700;
    font-size: 11px;
    padding-bottom: 1vh;
  }
  .default-msg{
    text-align: left;
    font-weight: 700;
    font-size: 11px;
    padding-bottom: 1vh;
  }
  .btn{
    width : 100%;
    height : 6vh;
    border: none;
    border-radius: 1vh;
    background-color:  hsl(234, 29%, 20%);
    color: white;
    font-family: "Roboto",sans-serif;
    font-weight: 400;
    font-size: 11px;
    font-weight: 700;
    letter-spacing: 0.3px;
  }
  
  .btn:hover{
    background-color: tomato;
    cursor: pointer;
  }
  
  .email >input{
    padding: 2vh;
    border-radius: 1vh;
    border: 1px solid hsl(231, 7%, 60%);
    
  }
  .Thank-you{
    display: none;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-style: normal;
    background-color: white;
    min-height: 100vh;
    align-items: center;
    justify-content: center;
  }
  
  .msg-container{
    display: flex;
    flex-direction: column;
    row-gap: 2vh;
    width: 100%;
    height: 100vh;
    border-radius: 5vh;
    background-color: white;
    color:hsl(234, 29%, 20%);
    justify-content: center;
    padding: 0 4vh;
  }
  
  .tq-symbol{
    background-image: url('../newsletter-sign-up-with-success-message-main/assets/images/icon-success.svg');
    width: 7vh;
    height: 7vh;
    background-repeat: no-repeat;
    background-size: contain;
  }
  .tq-header{
    font-size: 40px;
    font-weight: 700;
    line-height: 5vh;
  }
  
  .tq-msg{
    font-size: 12px;
    font-weight: 400;
    word-spacing: 2px;
    line-height: 2.25vh;
  }
  .tq-btn{
    border: none;
    width: 100%;
    height : 6vh;
    background-color: hsl(234, 29%, 20%);
    border-radius: 4px;
    color:white;
    font-weight: 700;
    font-size: 12px;
    justify-self: end;
  }
  .tq-btn:hover{
    background-color: hsl(4, 100%, 67%);
    cursor: pointer;
  }
}


